				"tema": "Indstria, Comrcio e Defesa do Consumidor (utilizado at a legislatura 53)",
				"ementa": "Altera e acresce dispositivos  Lei n 11.105, de 24 de maro de 2005.\n\nNOVA EMENTA: Altera a Lei n 11.105, de 24 de maro de 2005.",
				"explicacaoEmenta": "Estabelece que os rtulos dos alimentos destinados ao consumo humano informem ao consumidor a natureza transgnica do alimento.",
				"dataApresentacao": "16/10/2008",
				"regimeTramitacao": "Urgncia art. 155 RICD",
				"dataUltimoDespacho": "12/06/2009",
				"ultimoDespacho": "Tendo em vista o arquivamento do PL 5845/05, nos termos do art. 58,  4 do RICD, revejo o despacho aposto a este projeto, para determinar sua distribuio  Comisso de Desenvolvimento Econmico, Indstria e Comrcio; Comisso de Defesa do Consumidor e Comisso de Constituio e Justia e de Cidadania. \n\nEsclareo, por oportuno, que a proposio encontra-se Pronta para Ordem do dia, tendo pareceres, quando de sua tramitao conjunta ao PL 5.848/05, originalmente principal, da Comisso de Desenvolvimento Econmico, Indstria e Comrcio, pela aprovao do PL 5.848/05, com emenda (Relator: Dep. Fernando de Fabinho); da Comisso de Defesa do Consumidor pela rejeio do PL 5.848/05 (Relator: Dep. Barbosa Neto); e da Comisso de Constituio e Justia e de Cidadania, pela constitucionalidade, juridicidade e tcnica legislativa deste e pela inconstitucionalidade e injuridicidade do PL 5.848/05 e da emenda da Comisso de Desenvolvimento Econmico, Indstria e Comrcio (Relator: Dep. Ricardo Barros).",
				"apreciacao": "Proposio Sujeita  Apreciao do Plenrio",
