				"tipoProposicao": "Projeto de Lei",
				"tema": "Administrao Pblica; Direito Civil e Processual Civil; Direito Penal e Processual Penal; Organizao Poltica, Partidria e Eleitoral",
				"ementa": "Estabelece medidas contra a corrupo e demais crimes contra o patrimnio pblico e combate o enriquecimento ilcito de agentes pblicos.\n\nNOVA EMENTA: Estabelece medidas de combate  impunidade,  corrupo; altera os Decretos-Leis ns 2.848, de 7 de dezembro de 1940 - Cdigo Penal, e 3.689, de 3 de outubro de 1941 - Cdigo de Processo Penal; as Leis ns 4.717, de 29 de junho de 1965, 4.737, de 15 de julho de 1965, 8.072, de 25 de julho de 1990, 8.112, de 11 de dezembro de 1990, 8.429, de 2 de junho de 1992, 8.906, de 4 de julho de 1994, 9.096, de 19 de setembro de 1995,  9.504, de 30 de setembro de 1997, 9.613, de 3 de maro de 1998, e 7.347, de 24 de julho de 1985; revoga dispositivos do Decreto-Lei n 201, de 27 de fevereiro de 1967, e da Lei n 8.137, de 27 de dezembro de 1990; e d outras providncias.",
				"explicacaoEmenta": "Altera as Leis n 8.137, de 1990; 8.702, de 1990; 13.150,  de 2015;  8.429, de 1992; 9.096, de 1995; 9.504, de 1997; 9.613, de 1998 e os Decretos-leis n 2.848, de 1940; 3.689, de 1941. Revoga dispositivo do Decreto-lei n 201, de 1967.",
				"dataApresentacao": "29/03/2016",
				"regimeTramitacao": "Urgncia (Art. 155, RICD)",
				"dataUltimoDespacho": "17/02/2017",
				"ultimoDespacho": "Encaminhe-se  Secretaria-Geral da Mesa, nos termos do art. 252, V, do Regimento Interno da Cmara dos Deputados, para fins de verificao do atendimento do que dispe o art. 61,  2, da Constituio Federal. Informo que, excepcionalmente, sero levados em conta, para fins de certificao do cumprimento dos requisitos constitucionais referentes  propositura de projeto de lei de iniciativa popular, todos os registros que apresentarem informaes completas, em ateno  deciso liminar em epgrafe. Em seguida, submeta-se o processo ao Presidente da Comisso de Constituio e Justia e de Cidadania para anlise e ratificao do que certificado pela Secretaria-Geral da Mesa. Publique-se. Oficie-se.",
				"apreciacao": "Proposio Sujeita  Apreciao do Plenrio",
				"indexacao": "Alterao, Cdigo Penal, crime praticado por funcionrio pblico contra a administrao pblica, tipicidade, crime, enriquecimento ilcito, priso, confisco de bens, agravao penal, aumento, pena, peculato, concusso, excesso de exao (crime), corrupo passiva, corrupo ativa, estelionato, sistema, prescrio, crime, extino, prescrio retroativa, unificao, prazo prescricional, pretenso punitiva, pretenso executria, pena cominada  duplicao, prazo, prescrio, corrupo ativa, transao comercial, mbito internacional. \n_ Alterao, Lei dos Crimes contra a Ordem Tributria, supresso, dispositivo legal, regulao, especificao, crime, corrupo. _Alterao, Lei dos Crimes Hediondos, tipicidade, crime hediondo, peculato, insero de dados falsos em sistema de informao, concusso, excesso de exao(crime), corrupo passiva, corrupo ativa. _Alterao, Cdigo de Processo Penal,  recusa, tribunal de justia, recurso judicial, recurso protelatrio, prazo determinado, pedido de vista, disciplinamento, embargo de declarao, habeas corpus, recurso especial, recurso extraordinrio, agilizao,  justia, conceituao, prova ilcita, critrio, inadmissibilidade, excluso, ilicitude, precluso, nulidade. _Alterao, Cdigo de Processo Civil, recusa, tribunal de justia, recurso judicial, recurso protelatrio, prazo determinado, pedido de vista. _Alterao, Lei da Improbidade Administrativa, disciplinamento, acordo de lenincia, pessoa fsica, pessoa jurdica, improbidade administrativa, Ministrio Pblico. _Alterao, Lei Orgnica dos Partidos Polticos (1995), Lei das Eleies (1997), partido poltico, ato ilcito, administrao pblica, pas estrangeiro, utilizao indevida, recursos no contabilizados, bens, procedncia, infrao penal, sano, multa, ressarcimento, suspenso, cancelamento,  registro, prazo, competncia, Justia Eleitoral. _Alterao, Lei de Lavagem de Dinheiro, disciplinamento, cumprimento, instituio financeira, ordem judicial, quebra, transferncia, sigilo bancrio, sigilo fiscal, rastreamento, recursos financeiros, descumprimento, multa. _Disciplinamento, ao civil pblica, perda civil de bens, atividade ilcita. _Revogao, dispositivo legal, decreto-lei, crime de responsabilidade, prefeito, apropriao indbita, bens. _Critrio, percentual, recursos financeiros, publicidade, programa, marketing, combate, corrupo, Administrao Pblica, Cdigo de conduta.",
